import { useState } from 'react';
import { VscSaveAs } from 'react-icons/vsc';

const QuoteForm = ({ onAdd }) => {
    const [text, setText] = useState('')
    const [author, setAuthor] = useState('')

    const onSubmit = (e) => {
        e.preventDefault()

        onAdd({ text, author })

        setText('')
        setAuthor('')
    }

    return (
        <form className='quote-form' onSubmit={onSubmit}>
            <div className='form-group'>
                <label>Quote :</label>
                <input type='text' maxLength='210' placeholder='Enter your quote...' value={text} onChange={(e) => setText(e.target.value)} required />
            </div>
            <div className='form-group'>
                <label>Author :</label>
                <input type='text' maxLength='25' placeholder='Name of the author...' value={author} onChange={(e) => setAuthor(e.target.value)} required />
            </div>
            <button className='write-button'><VscSaveAs className='write-icon' /> Write</button>
        </form>
    )
}

export default QuoteForm;